import { IonContent, IonPage, IonAvatar, IonButton } from '@ionic/react';
import React from 'react';
import { Redirect } from 'react-router-dom';
import GetFitnessData from '../../domain/GetFitnessData';
import ConvertDataToValue from '../../domain/ConvertDataToValue';
import * as firebase from 'firebase/app';
import 'firebase/auth';
import GetFirebase from '../../domain/GetFirebase';
import GetSubscribedTier from '../../domain/GetSubscribedTier';
import UnsubscribeFromTier from '../../domain/UnsubscribeFromTier';
import GetPeriodStartDate from '../../domain/GetPeriodStartDate';

const flexContainer = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    'flex-direction': 'column',
    height: '100%'
}

class Home extends React.Component<any, any> {
    constructor(props: any) {
        super(props);
        // const accessToken = CheckLoggedIn();
        this.state = {
            redirectToLogin: false,
            redirectToSignUp: false,
            profileImage: '',
            givenName: '',
            tier: null,
            value: 0,
            points: 0,
            data: { steps: 0 },
        };        
        GetFirebase();
        if (firebase.auth().currentUser != null) this.userLoaded(firebase.auth().currentUser);
        firebase.auth().onAuthStateChanged((user: any) => { this.userLoaded(user) });
    }

    userLoaded(user: any) {
        if (user) {
            const token = localStorage.getItem('access-token') as string;
            GetSubscribedTier(user.uid, (tier: any) => { 
                console.log(tier);
                if (tier === null) {
                    this.setState((state: any) => ({ redirectToSignUp: true }));
                } else {
                    const start = GetPeriodStartDate(tier.period);
                    GetFitnessData(token, start, new Date(), (steps: number) => {
                        const result = ConvertDataToValue({ steps }, tier);
                        this.setState((state: any) => ({ value: result.amount, points: result.points, data: {steps}, tier }));
                    });
                }
            });
            this.setState((state: any) => ({ 
                profileImage: user.photoURL, 
                displayName: user.displayName 
            }));
        } else {
            this.setState((state: any) => ({ redirectToLogin: true }));
        }
    }

    logout() {
        firebase.auth().signOut()
        localStorage.removeItem('access-token');
        this.setState((state: any) => ({ redirectToLogin: true }));
    }

    unsubscribe() {
        UnsubscribeFromTier(() => {
            this.setState((state: any) => ({ redirectToSignUp: true }));
        });
    }

    render() {
        return (
            <IonPage>
                <IonContent>
                    {
                       this.state.redirectToLogin ? <Redirect to='/login'/> : <div></div>
                    }
                    {
                       this.state.redirectToSignUp ? <Redirect to='/signup'/> : <div></div>
                    }
                    <div style={flexContainer}>
                        <h1>ATLAS BOUNTY</h1>
                        <IonAvatar>
                            <img src={this.state.profileImage}/>
                        </IonAvatar>
                        <h2>{this.state.displayName}</h2>
                        <div className='font value-display'>
                            <span style={{fontSize: '1em'}}>$</span><span style={{fontSize: '4em'}}>{this.state.value}</span>
                            <span style={{fontSize: '1em'}}>/{(this.state.tier) ? this.state.tier.cost : ''}</span>
                        </div>
                        <div className='value-display'>
                            <div className='label' style={{ fontSize: '2em' }}>{this.state.points}</div>
                            <div style={{ fontSize: '0.8em' }}>POINTS THIS {
                                (this.state.tier) ? this.state.tier.period.toUpperCase() : ''
                            }</div>
                        </div>
                        <div className='value-display'>
                            <div className='label' style={{ fontSize: '2em' }}>{this.state.data.steps}</div>
                            <div style={{ fontSize: '0.8em' }}>STEPS THIS {
                                (this.state.tier) ? this.state.tier.period.toUpperCase() : ''
                            }</div>
                        </div>
                        <div style={{flexGrow: 1}}></div>
                        <IonButton onClick={() => this.logout()} fill={'clear'}>Sign Out</IonButton>
                        <IonButton onClick={() => this.unsubscribe()} fill={'clear'}>Unsubscribe</IonButton>
                    </div>
                </IonContent>
            </IonPage>
        );
    }
}

export default Home;
