import GetFirebase from "./GetFirebase"

const GetSubscriptionTiers = (callback: (tiers: any) => void): any => {
    const app = GetFirebase();
    const db = app.firestore();
    db.collection('tiers').get().then((snapshot: any) => {
        const tiers = snapshot.docs.map((doc: any) => ({ id: doc.id, ...doc.data() }));
        callback(tiers);
   });
}

export default GetSubscriptionTiers;