import { IonContent, IonPage, IonButton, IonCard, IonCardHeader, IonCardTitle, IonCardSubtitle, IonCardContent, IonCheckbox } from '@ionic/react';
import React from 'react';
import { Redirect } from 'react-router';
import GetSubscriptionTiers from '../../domain/GetSubscriptionTiers';
import SubscribeToTier from '../../domain/SubscribeToTier';
import * as firebase from 'firebase/app'

const flexContainer = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    'flex-direction': 'column',
    height: '100%'
}

class SignUp extends React.Component<any, any> {
    constructor(props: any) {
        super(props);
        this.state = {
            redirectToHome: false,
            redirectToLogin: false,
            tiers: [],
        };
        GetSubscriptionTiers((tiers: any) => {
            this.setState((state: any) => ({ tiers }));
        });
    }

    didClickTier(tier: any) {
        SubscribeToTier(tier.id, () => {
            this.setState((state: any) => ({ redirectToHome: true }));
        });
    }
    
    logout() {
        firebase.auth().signOut()
        localStorage.removeItem('access-token');
        this.setState((state: any) => ({ redirectToLogin: true }));
    }

    render() {
        return <IonPage>
            <IonContent>
            {
                this.state.redirectToLogin ? <Redirect to='/login'/> : <div></div>
            }
                <div style={flexContainer}>
                    <h1>ATLAS BOUNTY</h1>
                    <h2>Subscribe</h2>
                    {
                        this.state.redirectToHome ? <Redirect to="/home"/> : <div></div>
                    }
                    {
                        this.state.tiers.map((tier: any) => (
                            <IonCard className='hover-card' onClick={() => {this.didClickTier(tier)}}>
                                <IonCardHeader style={{width: 350}}>
                                    <IonCardSubtitle>
                                        { tier.name.toUpperCase() }
                                    </IonCardSubtitle>
                                    <IonCardTitle>
                                        $<span style={{fontSize: '3em'}}>{ tier.cost }</span>/{tier.period}
                                    </IonCardTitle>
                                    <IonCardContent>
                                        {tier.description}
                                    </IonCardContent>
                                </IonCardHeader>
                            </IonCard>
                        ))
                    }
                    <IonButton onClick={() => this.logout()} fill={'clear'}>Sign Out</IonButton>
                </div>
            </IonContent>
        </IonPage>
    }
    
}

export default SignUp;
