import GetFirebase from "./GetFirebase";

const GetSubscribedTier = (uid: string, callback: (tier: any | null) => void) => {
    const app = GetFirebase();
    const db = app.firestore();
    if (uid !== null) {
        db.collection('users').doc(uid).get().then((snapshot: any) => {
            if (snapshot.exists) {
                const tierId = snapshot.data().tier;
                db.collection('tiers').doc(tierId).get().then((snapshot: any) => {
                    if (snapshot.exists) {
                        callback(snapshot.data());
                    } else {
                        callback(null);
                    }
                });
            } else {
                callback(null);
            }
        }).catch((err: any) => {
            console.log(err);
            callback(null);
        });
    }
   
}

export default GetSubscribedTier;