import request from 'request';

// const nowDate = new Date();
// const firstDayOfMonth = new Date();
// firstDayOfMonth.setDate(1);
// firstDayOfMonth.setHours(0, 0, 0, 0);

const GetFitnessData = (
    accessToken: string, 
    startDate: Date, 
    endDate: Date, 
    callback: (stepCount: number) => void
) => {
    const requestData = {    
        "aggregateBy" : [{    
            "dataSourceId": "derived:com.google.step_count.delta:com.google.android.gms:estimated_steps"    
        }],    
        "bucketByTime": { "durationMillis": 86400000 }, // This is 24 hours    
        "startTimeMillis": startDate.getTime(),   // Start time    
        "endTimeMillis": endDate.getTime() // End Time    
    };
    request.post('https://www.googleapis.com/fitness/v1/users/me/dataset:aggregate', {
        json: requestData,
        headers: {
            'Authorization': 'Bearer ' + accessToken
        }
    }, (error: any, res: any, body: any) => {
        if (error || body.error) {
            console.log(body);
            return;
        }
        const result = body.bucket.flatMap((bucket: any) => {
            return bucket.dataset.flatMap((set: any) => {
                return set.point.flatMap((point: any) => {
                    return point.value.flatMap((val: any) => val.intVal);
                });
            });
        }).reduce((a: number, b: number) => a + b);
        callback(result);
    });
}

export default GetFitnessData;