import { IonContent, IonPage, IonButton } from '@ionic/react';
import React from 'react';
import { Redirect } from 'react-router';
import * as firebase from 'firebase/app';
import 'firebase/auth';
import GetFirebase from '../../domain/GetFirebase';

const flexContainer = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    'flex-direction': 'column',
    height: '100%'
}

class SignIn extends React.Component<any, any> {
    constructor(props: any) {
        super(props);
        this.state = {
            redirectToHome: false
        }
        GetFirebase();
    }

    signin() {
        const provider = new firebase.auth.GoogleAuthProvider();
        provider.addScope('https://www.googleapis.com/auth/fitness.activity.read')
        firebase.auth().signInWithPopup(provider).then((result: any) => {
            const token = result.credential.accessToken;
            localStorage.setItem('access-token', result.credential.accessToken);
            // localStorage.setItem('profile-image', result.user.photoURL);
            // localStorage.setItem('given-name', result.user.displayName);
            this.setState((state: any) => ({ redirectToHome: true }));
        });
    }

    render() {
        return <IonPage>
            <IonContent>
                <div style={flexContainer}>
                    <div style={{ flexGrow: 1}}></div>
                    <h1>ATLAS BOUNTY</h1>
                    <h2>The world is your bounty.</h2>
                    <div style={{ flexGrow: 1}}></div>
                    <IonButton onClick={() => this.signin()}>
                        Log In With Google
                    </IonButton>
                    {
                        this.state.redirectToHome ? <Redirect to="/home"/> : <div></div>
                    }
                    <div style={{ flexGrow: 1}}></div>
                </div>
            </IonContent>
        </IonPage>
    }
    
}

export default SignIn;
