const ConvertDataToValue = (data: { steps:number }, tier: any): { amount: number, points: number} => {
    const rate = 0.0001;
    const dataValue = Math.floor(data.steps * rate * 100)/100;
    const amount = Math.min(dataValue, tier.cost);
    let points = 0;
    if (dataValue > tier.cost) {
        points = Math.floor((dataValue - tier.cost) * 100);
    }
    return { amount: amount, points: points };
}

export default ConvertDataToValue;